import React from 'react'

const PortfolioHeader = ({}) => {
	const getTop = ()=>{
		let middle = document.getElementById("about-main");
		let lower = document.getElementById("middle-panel");
		let top = document.getElementById('portfolio-divider');

		if(!lower || !middle || !top){return "-10vh"};
		middle = middle.getBoundingClientRect().bottom
		lower= lower.getBoundingClientRect().top
		top = top.getBoundingClientRect().top
		let center = (middle + top)
		let h = (window.innerHeight -top + 80)/window.innerHeight
		if(middle > window.innerHeight){return "-10vh"}
		console.log(middle, lower);
		return (middle*(1-h) + top*h -40 -lower)

	}
	return(
		<div className="featured-work-header"
		style={{top: getTop()}}
		>
			Portfolio
		</div>
	)
}
export default PortfolioHeader;


