import React from 'react'
import steve from '../../images/steves.png'
import classnames from 'classnames'



class ContentBlock extends React.Component{
	constructor(){
		super();
	}
	
	getClass(id){ 
		let node = document.getElementById(id);
		if(!node){
			return classnames('content-container', 'clearfix', 'show');
		}
		let top = node.getBoundingClientRect().top;
		return classnames('content-container', 'clearfix', {show:  top<window.innerHeight*.75},{hide: top>window.innerHeight*.75} );

	}
	componentDidMount(){

	}
	render(){
		let data = this.props.data
		
		return (
			<div  className={this.getClass(data.title + '-container')} id={data.title + '1'}>
				<div className="content-title" id={data.title}>{data.title}</div>
				<div className="clearfix">
					<div className="pic-div" style={{backgroundImage: 'url(../../images/profback.png)'}}>
						<img className="pic" src={steve}/>
					</div>
					<div className="list-stuff">
						<ul>
							{data.list.map(x=>(
								<li>{x}</li>
								))}
						</ul>
					</div>
				</div>
				<br/>
				<div className="content-description">
					{data.description}
				</div>
			</div>
		)
	}
}

export default ContentBlock;


