import React from 'react'

const FeaturedWorkHeader = ({}) => {
	const getTop = ()=>{
		let upper = document.getElementById("featured-inner-container");
		let lower = document.getElementById("middle-panel");

		if(!lower || !upper){return "10vh"};
		return ( upper.getBoundingClientRect().top/2 -lower.getBoundingClientRect().top)

	}
	return(
		<div className="featured-work-header"
		style={{top: getTop()}}
		>
			Featured Work
		</div>
	)
}

export default FeaturedWorkHeader;


