import React from 'react'
import ContentBlock from './ContentBlock'
import classnames from 'classnames'

const Container = ({data, page, back}) =>{
	let col = back? '#aaa': '#fff'
	let backClass = classnames({backcontainer: back })
	return(
	<div className={backClass} style={{
		background: 'linear-gradient(to right, #fff 20%,'+col+')', 
		width: '85vw'
	}}>
		<div className="content-block-container" id={data.title+'-container'} >
			<ContentBlock data={data} page={page}/>
		</div>
	</div>
	)}

export default Container


