import React from 'react'
import pic from '../images/mecirc.png'

const LeftPanel = () => (
	<div className="left-panel-main" id='left-panel'>
		<div className="mecirc"></div>
		<div className="nav-container">
			<div className="nav-li nav-li-top clickable">Featured Work</div>
			<div className="nav-li clickable">About</div>
			<div className="nav-li nav-pers-container">
				Personal Projects
				<div className="nav-pers clickable ">Data Mapper</div>
				<div className="nav-pers clickable ">Checkers AI</div>
			</div>
			<div className="nav-li nav-prof-container">
				Professional Projects
				<div className="nav-prof clickable "
					onClick={()=>document.getElementById("Steve's Place4").scrollIntoView({behavior:'smooth', block:'start'})}
				>Steves Place</div>
				<div className="nav-prof clickable ">Wind Horse Circle</div>
			</div>
			<div className="nav-li clickable">Contact Me</div>
		</div>
		
	</div>
)

export default LeftPanel;


