import React from 'react'
import Container from './ContentBlock/Container'
import data from '../data/data'
import FeaturedWork from './FeaturedWork/FeaturedWork'
import FeaturedWorkHeader from './FeaturedWork/FeaturedWorkHeader'
import About from './About/AboutContainer'
import AboutHeader from './About/AboutHeader'
import PortfolioHeader from './ContentBlock/PortfolioHeader'

class RightPanel extends React.Component{
	constructor(){
		super();
		this.state={page: null}
	}
	componentDidMount(){
		window.addEventListener('scroll', (e)=>{this.setState({page:e})})
	}

	render(){
		return(
			<div className="middle-panel-main" id="middle-panel">
				<FeaturedWorkHeader page={this.state.page}/>
				<FeaturedWork data={data[0]}/>
				<AboutHeader page={this.state.page}/>
				<About/>
				<PortfolioHeader page={this.state.page}/>
				<div className="portfolio-divider" id="portfolio-divider"></div>
				{data.map((datum,i)=>(
					<Container data={datum} page={this.state.page} back={i%2}/>
					))}
				
			</div>
		)
	}
}

export default RightPanel;


